.manual {
  padding-top: 26px;

  @include viewport--lg {
    display: flex;
    flex-wrap: wrap;
  }
}

.manual__title {
  margin-bottom: 35px;
  width: 100%;

  @include viewport--md {

  }
}

.manual__list {
  background-color: $color-pink-1;
  list-style-type: none;
  padding: 48px 0 48px 103px;
  margin: 0;
  flex-grow: 1;

  @include viewport--lg {
    padding: 56px 0 0 0;
  }
}

.manual__item {
  background-color: $color-default-white;
  display: flex;
  align-items: center;
  padding: 24px 7px 22px 68px;
  position: relative;
  margin-bottom: 60px;

  @include viewport--lg {
    max-width: 461px;
    height: 70px;
    border-radius: 241px;
  }

  &::before {
    content: "";
    width: 119px;
    height: 119px;
    border: 4px solid $color-purple;
    border-radius: 50%;
    position: absolute;
    left: -69px;
    background-image: url('../img/content/guide-img-1.jpg');
    background-size: cover;
  }

  p {
    margin: 0;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: $color-black-2;

  }

  .item-count {
    font-size: 49px;
    line-height: 60px;
    font-weight: 700;
    color: $color-purple;
    margin-right: 20px;
    display: block;
  }
}

.manual__item--step-2::before {
  background-image: url('../img/content/guide-img-2.jpg');
}

.manual__item--step-3::before {
  background-image: url('../img/content/guide-img-3.jpg');
}

.manual__item--step-4::before {
  background-image: url('../img/content/guide-img-4.jpg');
}

.manual-image {
  max-width: 100%;
  display: flex;

  @include viewport--lg {
    max-width: 776px;
  }
}
