.header {
  display: flex;
  justify-content: center;
  min-height: 71px;
  padding: 24px 0 16px;
  background-color: $color-default-white;
  max-width: 480px;
  margin: 0 auto;

  @include viewport--lg {
    background-color: $color-pink;
    max-width: unset;
  }



  &__wrapper {
    max-width: 480px;
    margin: 0 auto;

  }

  &__logo {
    text-transform: uppercase;
    font-weight: 300;
    font-size: 25px;
    line-height: 31px;

    @include viewport--lg {
      padding-left: 30px;
    }
  }
}
