

.product-info {
  max-width: 480px;


  @include viewport--lg {
    max-width:568px;
    margin-top: -4px;
  }
}

.product-info .wrapper {
  padding: 0 19px 0 31px;
  display: flex;
  flex-wrap: wrap;

  @include viewport--lg {
    padding: 0;
  }
}

.product-info__title {
  margin-bottom: 12px;
  margin-top: 26px;

  @include viewport--lg {
    margin-top: 0;
    margin-bottom: 0;
  }
}

.product-info__subtitle {
  margin-bottom: 14px;

  @include viewport--lg {
    margin-bottom: 11px;
  }
}

.product-info__text {
  margin-bottom: 14px;

  @include viewport--lg {
    margin-bottom: 11px;
  }
}

.product-info__statistics {
  display: flex;
  align-items: center;
  color: $color-gray-2;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  margin-bottom: 16px;

  @include viewport--lg {
    margin-bottom: 16px;
  }
}

.product-rating {
  display: flex;
  align-items: center;
  padding-left: 3px;

  svg {
    margin-right: 2.51px;

    &:last-of-type {
      margin-right: 12.52px;
    }
  }

  span {
    padding-right: 14px;
    position: relative;

    &::after {
      content: "";
      width: 1px;
      height: 19px;
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      background-color: $color-gray-2;
    }
  }
}

.product-reviews {
  padding-left: 14px;
  padding-right: 4px;
  position: relative;

  &::after {
    content: "";
    width: 1px;
    height: 19px;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    background-color: $color-gray-2;
  }
}

.product-orders {
  padding-left: 11px;
}

.product-info__delivery {
  padding-left: 37px;
  position: relative;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  padding-bottom: 14.59px;
  border-bottom: 1px solid $color-gray-6;
  margin-bottom: 13px;
  display: block;
  width: 100%;

  @include viewport--lg {
    margin-left: 8px;
    margin-bottom: 23px;

    width: 100%;
  }

  &::before {
    content: url('../img/svg/box.svg');
    width: 21.94px;
    height: 23.41px;
    position: absolute;
    left: 0;
    top: 29%;
    transform: translateY(-50%);
  }
}

.product-price {
  display: flex;
  align-items: center;
  margin-bottom: 6px;

  @include viewport--lg {
    flex-wrap: wrap;
    width: 248px;
    margin-bottom: 15px;
  }
}

.product-price__value {
  font-size: 48px;
  line-height: 59px;
  font-weight: 700;
  margin-right: 19px;

  @include viewport--lg {
    order: 3;
  }
}

.product-price__old {
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
  text-transform: uppercase;
  position: relative;
  top: 5px;
  color: $color-gray-2;
  margin-right: 22px;

  @include viewport--lg {
    order: 1;
    margin-right: auto;
    margin-left: 10px;
  }

  &::before {
    content: '';
    width: 67px;
    height: 2px;
    position: absolute;
    left: -10px;
    top: 55%;
    background-color: $color-red-2;
    transform: translateY(-50%);
  }
}

.product-price__discount {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: $color-orange;
  display: flex;
  align-items: center;
  justify-content: center;
  color: $color-default-white;
  font-weight: 600;
  font-size: 16px;
  line-height: 114.9%;
  margin-top: -10px;

  @include viewport--lg {
    order: 2;
  }
}

.product-quantity {
  display: flex;
  flex-direction: column;
  width: 140px;
  margin-top: -4px;

  @include viewport--lg {
    flex-direction: row;
    align-items: center;
    margin-bottom: 32px;
    width: 100%;
    margin-top: 0;
  }



  label {
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: $color-gray-2;
    margin-bottom: 13px;

    @include viewport--lg {
      margin-bottom: 0;
      margin-right: 21px;
    }
  }
}

.product-quantity__controls {
  display: inline;
  border: 1px solid $color-gray-5;
  border-radius: 29px;
  overflow: hidden;
  align-self: flex-start;
}

.control-button {
  background-color: $color-gray-4;
  border: none;
  padding: 0;
  width: 39.55px;
  height: 37px;
  font-size: 20px;
  line-height: 99.19%;
  color: $color-gray-1;
}

.product-quantity__field {
  border: none;
  width: 34px;
  text-align: center;
  font-size: 20px;
  line-height: 99.19%;
  color: $color-gray-1;
}

.product-discount {
  display: flex;
  flex-direction: column;
  color: $color-black-1;
  padding-left: 21px;
  position: relative;
  padding-bottom: 19px;
  width: 242px;
  order: 1;
  margin-left: auto;
  margin-bottom: 43px;

  @include viewport--lg {
    margin-left: auto;
    margin-right: 18px;
    width: unset;
    margin-bottom: 0;
    order: 0;

  }
}

.product-buy__discount {
  margin-bottom: 42px;
}

.product-discount__description {
  font-weight: 600;
  font-size: 14px;
  line-height: 109.9%;
  margin: 0;
  position: relative;
  margin-bottom: 5px;

  &::before {
    content: url('../img/svg/clock.svg');
    width: 16px;
    height: 16px;
    position: absolute;
    left: -21px;
    top: 50%;
    transform: translateY(-50%);
  }
}

.product-timer {
  display: flex;
  padding-left: 7px;
}

.product-timer__item {
  font-weight: 500;
  font-size: 25px;
  line-height: 30px;
  padding-top: 5px;
  margin-right: 16px;
  background-color: $color-yellow;
  width: 60px;
  height: 36px;
  display: flex;
  justify-content: center;
  position: relative;

  &:last-of-type {
    margin-right: 0;
  }

  &:after {
    content: 'часы';
    display: block;
    height: 19px;
    background-color: $color-yellow;
    position: absolute;
    bottom: -18px;
    width: 100%;
    font-size: 12px;
    line-height: 1;
    text-align: center;
  }
}

.product-timer__minutes {
  &:after {
    content: 'минуты';
  }
}

.product-timer__seconds {
  &:after {
    content: 'секунды';
  }
}


.product-timer__hours {
  position: relative;

  &::before {
    content: ':';
    font-weight: 500;
    font-size: 34px;
    line-height: 34px;
    padding-top: 5px;
    position: absolute;
    right: -12px;
    top: -3px;
  }
}

.product-timer__minutes::before {
  content: ':';
  font-weight: 500;
  font-size: 34px;
  line-height: 34px;
  padding-top: 5px;
  position: absolute;
  right: -11px;
  top: -3px;
}

.product-buy {

}

.product-buy__button {
  background-color: $color-orange;
  width: 100%;
  border: none;
  height: 64px;
  text-transform: uppercase;
  color: $color-default-white;
  padding-bottom: 10px;


  @include viewport--lg {
    width: 277px;
    padding-left: 19px;
    padding-top: 9px;
    height: 59px;
    padding-bottom: 0;
  }

  .button-text {
    font-size: 18px;
    line-height: 99.19%;
    font-weight: 700;
    margin-right: 18px;

    @include viewport--lg {
      margin-right: 0;
    }
  }

  .button-price {
    font-size: 33px;
    line-height: 40px;
    font-weight: 800;
    margin-right: 27px;

    @include viewport--lg {
      display: none;
    }
  }

  .button-oldPrice {
    font-weight: 500;
    font-size: 24px;
    line-height: 29px;
    color: $color-black-3;
    position: relative;


    @include viewport--lg {
      display: none;
    }

    &::before {
      content: '';
      display: block;
      width: 67px;
      height: 2px;
      background-color: $color-black-3;
      position: absolute;
      top: 52%;
      left: -9px;
      transform: translateY(-50%);
    }
  }
}
